const getLocationByZipcode = (zipCode) => locations[zipCode];

const getZipCodesByLocation = (value) =>
  Object.keys.filter((key) => locations[key] === value);

const getLocationByLetters = (letters) =>
  [
    ...new Set(
      Object.values(locations).filter((location) =>
        location.toLowerCase().startsWith(letters.toLowerCase())
      )
    )
  ].slice(0, 5);

export { getLocationByZipcode, getZipCodesByLocation, getLocationByLetters };
